.footer.footer {
    background-color: #777;
    line-height: 36px;
    width: 100%;
    margin: 0;
}

.footer {
    background-attachment: fixed;
    position: relative;
    line-height: 20px;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

nav.footer-nav {
    position: absolute;
    line-height: 85px;
    display: block;
}

.footer .credits {
    line-height: 85px;
}

.ml-auto {
    margin-left: auto !important;
}

.footer nav > ul {
    list-style: none;
    margin: 0;
    padding: 0;
    font-weight: normal;
}

ul {
    margin-top: 0;
    margin-bottom: 1rem
}

.footer:not(.footer-big) nav ul li:first-child {
    margin-left: 0;
}

.footer nav > ul > li {
    display: inline-block;
    padding: 10px 15px;
    margin: 15px 3px;
    line-height: 20px;
    text-align: center;
}

.footer nav > ul a:not(.btn) {
    color: #ffffff;
    display: block;
    margin-bottom: 3px;
    line-height: 1.6;
    opacity: .8;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
}

a {
    color: #51bcda;
}
a {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: "Montserrat", "Helvetica", Arial, sans-serif;
    font-weight: 300;
}

a {
    color: #4897fd;
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects
}

.footer .copyright {
    color: #A49E9E;
    font-size: 0.9em;
}

.footer .copyright {
    color: #ffffff;
    padding: 10px 0px;
    font-size: 14px;
    margin: 15px 3px;
    line-height: 20px;
    text-align: center;
}

i.fa.fa-heart.heart {

}

.footer .heart {
    color: #EB5E28;
}

.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.fa-heart:before {
    content: "\f004"
}

.fa {
    width: 18px;
    text-align: center;
}

.heart {
    color: #EB5E28;
    animation: heathing 1s ease infinite;
}